@keyframes circle-container_tablet2x {
  from {
    top: calc(-100vh - 95px);
    left: -889px;
    width: 0;
    height: 0;
  }
  to {
    top: -84px;
  }
}

@keyframes circle-container_tablet {
  from {
    transform: translate(-145px, -145px);
  }
  to {
    transform: translate(0, 0);
  }
}
@keyframes entrance-enlarge {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@keyframes circle-container__ring_tablet2x {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
@keyframes nudge_light {
  from {
  }
  to {
    transform: translate(3px, -3px);
  }
}

@keyframes vibrate-2 {
  0% {
    transform: translate(0);
  }
  5% {
    transform: translate(-2px, -2px);
  }
  10% {
    transform: translate(2px, -2px);
  }
  15% {
    transform: translate(-2px, 2px);
  }
  20% {
    transform: translate(2px, 2px);
  }
  25% {
    transform: translate(-2px, -2px);
  }
  30% {
    transform: translate(2px, -2px);
  }
  35% {
    transform: translate(-2px, 2px);
  }
  40% {
    transform: translate(-2px, -2px);
  }
  45% {
    transform: translate(2px, -3px);
  }
  50% {
    transform: translate(0);
  }
  55% {
    transform: translate(-2px, -2px);
  }
  60% {
    transform: translate(2px, -2px);
  }
  65% {
    transform: translate(-2px, 2px);
  }
  70% {
    transform: translate(2px, 2px);
  }
  75% {
    transform: translate(-2px, -2px);
  }
  80% {
    transform: translate(2px, -2px);
  }
  85% {
    transform: translate(-2px, 2px);
  }
  90% {
    transform: translate(-2px, -2px);
  }
  95% {
    transform: translate(2px, -3px);
  }
  100% {
    transform: translate(0);
  }
}

@keyframes vibrate-3 {
  0% {
    transform: translate(0);
  }
  10% {
    transform: translate(-3px, -3px);
  }
  20% {
    transform: translate(3px, -3px);
  }
  30% {
    transform: translate(-3px, 3px);
  }
  40% {
    transform: translate(3px, 3px);
  }
  50% {
    transform: translate(-3px, -3px);
  }
  60% {
    transform: translate(3px, -3px);
  }
  70% {
    transform: translate(-3px, 3px);
  }
  80% {
    transform: translate(-3px, -3px);
  }
  90% {
    transform: translate(3px, -3px);
  }
  100% {
    transform: translate(0);
  }
}

@keyframes brand-logo_tablet2x {
  from {
    opacity: 0;
    top: 50px;
  }
  to {
    opacity: 1;
    top: 40px;
  }
}

@keyframes fade-in-bottom {
  0% {
    transform: translateY(18px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
@keyframes images-entrance-slide {
  from {
    transform: rotate(20deg) translate(-647px, -70px);
  }
  to {
    transform: rotate(-10deg) translate(0px, 0px);
  }
}

@keyframes circle-entrance-slide {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
